import React from 'react';
import Volume from "./Volume";
const ConfigPanel = ({ volume, setVolume, openQueue, color, showQueue }) => {
    return (React.createElement("div", { className: 'config-panel' },
        React.createElement(Volume, { volume: volume, setVolume: setVolume, openQueue: openQueue, color: color, showQueue: showQueue })));
};
export default ConfigPanel;
