import React, { useEffect, useRef } from "react";
import QueueSong from "./QueueSong";
import { faXmark } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import "../../style/queue.css";
const Queue = ({ songs, removeSong, playSong, queuePopUp, onQueueOpen, currentIndex, }) => {
    const ref = useRef(null);
    const handleClickOutside = (event) => {
        if (ref.current && !ref.current.contains(event.target)) {
            onQueueOpen(false);
        }
    };
    useEffect(() => {
        document.addEventListener("click", handleClickOutside, true);
        return () => {
            document.removeEventListener("click", handleClickOutside, true);
        };
    }, []);
    return (React.createElement("div", { className: queuePopUp ? "queue-container" : "queue-container hidden", ref: ref },
        React.createElement("div", { className: "queue-header" },
            React.createElement("h4", { draggable: false }, "Queue"),
            React.createElement("div", { className: "close-btn", onClick: () => onQueueOpen(false) },
                React.createElement(FontAwesomeIcon, { icon: faXmark, color: "#FFF" }))),
        React.createElement("div", { className: "queue-list" }, songs.map((song, i) => {
            return (React.createElement(QueueSong, { currentIndex: currentIndex, image_src: song.song_cover, song_artist: song.song_artist, song_title: song.song_title, index: i, key: song.id, audio_src: song.url, removeSong: removeSong, playCurrentSong: playSong }));
        }))));
};
export default Queue;
